





































import {
  Component, Prop, Vue, Watch,
} from 'vue-property-decorator';
import { divideNumberWithSpaces } from '@/utils/common';
import Rate from '@/components/Rate.vue';
import AmountChooser from '@/components/AmountChooser/vue-amount-chooser.vue';
import { createRequest } from '@/services/http.service';
import { endpoints } from '@/config';
import $store from '@/store';
import { OrderResponse } from '@/models/responses';
import router from '@/router';
import { Product } from '@/models/order';
import VueScrollTo from 'vue-scrollto';

@Component({
  components: { AmountChooser, Rate },
})
export default class ItemInfo extends Vue {
  @Watch('isAuthorized') isAuthorizedChanged(val) {
    if (val && this.lastOrderAction) {
      this.$nextTick(() => {
        this.lastOrderAction();
      });
    }
  }

  @Prop() public item!: Product;

  selectedOptions: any = null

  itemAmount = 1;

  pending = false;

  isOrderDisabled = true;

  validated = false;

  animated = false;

  lastOrderAction: any = undefined;

  get isAuthorized() {
    return (this as any).$auth.check();
  }

  get maxCount() {
    return this.item.maxCount && this.item.maxCount < 99 ? this.item.maxCount : 99;
  }

  updateOrderState() {
    if (!this.selectedOptions) {
      return;
    }
    const allValuesSelect = Object.values(this.selectedOptions).filter(Boolean).length === Object.keys(this.selectedOptions).length;
    const isDisabled = this.pending || !allValuesSelect;
    this.$emit('order-disabled', isDisabled);
    this.isOrderDisabled = isDisabled;
  }

  optionSelect(parentId, id) {
    if (parentId && id) {
      this.selectedOptions = {
        ...this.selectedOptions,
        [parentId]: id,
      };
    }
    this.updateOrderState();
  }

  toggleFav() {
    if (this.item.isFavourite) {
      this.removeFromFav();
    } else {
      this.addToFav();
    }
  }

  addToFav() {
    createRequest('GET', endpoints.favourites.addProduct(this.item.product_id))
      .then(() => {
        this.$root.$emit('show-toast', {
          message: 'Продукт добавлен в избранное',
        });
        this.updateFavourites();
      });
  }

  removeFromFav() {
    createRequest('GET', endpoints.favourites.deleteProduct(this.item.product_id))
      .then(() => {
        this.$root.$emit('show-toast', {
          message: 'Продукт удален из избранного',
        });
        this.updateFavourites();
      });
  }

  updateFavourites() {
    this.item.isFavourite = !this.item.isFavourite;
    $store.dispatch('app/updateFavouritesCount');
  }

  prepareData(type: 'self' | 'group', group?: Product | undefined) {
    const data: any = {
      id: this.item.product_id,
      options: Object.values(this.selectedOptions),
      count: this.itemAmount,
    };
    if (group) {
      data.group_id = group.id;
    }
    if (type === 'self') {
      data.self_price = this.item.selfPrice;
    } else {
      data.group_price = this.item.groupPrice;
    }
    return data;
  }

  sendOrder(type: 'self' | 'group', group?: Product | undefined) {
    if (!this.isAuthorized) {
      this.$root.$emit('show-login-modal');
      this.lastOrderAction = this.sendOrder.bind(this, type, group);
      return;
    }
    const data = this.prepareData(type, group);
    const url = type === 'self' ? endpoints.order.self : endpoints.order.group;
    this.pending = true;
    createRequest('POST', url, data)
      .then((res: OrderResponse) => {
        const orderId = res.data.data.oid;
        if (orderId) {
          router.push({ path: `/order/${orderId}` });
        }
      })
      .catch(() => {
        this.pending = false;
      });
  }

  buySelf() {
    if (this.isOrderDisabled) {
      this.validateOptions();
      return;
    }
    this.sendOrder('self');
  }

  buyGroup() {
    if (this.isOrderDisabled) {
      this.validateOptions();
      return;
    }

    this.sendOrder('group');
  }

  divideNumberWithSpaces(number) {
    return divideNumberWithSpaces(number);
  }

  mounted() {
    this.selectedOptions = {};
    Object.keys(this.item.options)
      .forEach((option: string) => {
        this.selectedOptions[option] = '';
      });
  }

  validateOptions() {
    this.animated = false;
    this.$nextTick(() => {
      this.validated = true;
      setTimeout(() => {
        const erroredEl = document.querySelector('.item-info__option-title--error');
        if (erroredEl) {
          setTimeout(() => {
            this.animated = true;
          }, 100);
          VueScrollTo.scrollTo(erroredEl, 100, { offset: -30 });
        }
      }, 100);
    });
  }
}

